  z-index: 1;
  position: relative;
  display: flex;
  align-items: center;
  height: 100%;
  padding: 0 8px 0 20px;
}

.iti__arrow {
  margin-left: 8px;
  width: 0;
  height: 0;
  border-left: 3px solid transparent;
  border-right: 3px solid transparent;
  border-top: 4px solid;
  transition: transform 0.4s;
}

.iti__arrow--up {
  transform: rotate(-180deg);
}

