svg,
.icon {
  width: 1em;
  height: 1em;
  display: inline-block;
  color: inherit;
  fill: currentColor;
}

.container {
  width: 100%;
  max-width: 1240px;
  margin: 0 auto;
  padding: 0 var(--gap-fluid);
}

.container-fluid {
  width: 100%;
  padding: 0 var(--gap-fluid);
}

.color-accent {
  color: #797DF2;
}

.img-cover {
  display: block;
  position: relative;
  overflow: hidden;
}
.img-cover img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  object-fit: cover;
}

.area-click, .btn-close {
  --indent: -5px;
  position: relative;
}
.area-click:before, .btn-close:before {
  content: "";
  position: absolute;
  top: var(--indent);
  right: var(--indent);
  bottom: var(--indent);
  left: var(--indent);
}

.link-cover:before {
  width: 100%;
  height: 100%;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
}

.btn-reset, .btn-close {
  background: transparent;
  margin: 0;
  padding: 0;
  font-size: 0;
  outline: none;
  border: none;
  border-radius: 0;
}

.btn {
  max-width: 100%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-style: normal;
  font-weight: bold;
  line-height: 1.35;
  text-align: center;
  text-transform: uppercase;
  border: none;
  border-radius: 0;
  outline: none;
  user-select: none;
  transition: all 0.4s;
  position: relative;
  cursor: pointer;
  overflow: hidden;
}
.btn:hover {
  text-decoration: none;
}
.btn.disabled, .btn:disabled {
  opacity: 0.8;
  cursor: not-allowed;
  pointer-events: none;
}
.btn--theme-accent {
  background-color: #797DF2;
  color: #fff;
  border-radius: 35px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
  animation: bg-move linear 4s infinite;
}
.btn--theme-accent:hover {
  background-color: #5A5FDA;
}
.btn--theme-accent.disabled, .btn--theme-accent:disabled {
  pointer-events: none;
  opacity: 0.5;
}
.btn--size-md {
  min-height: 60px;
  padding: 17px 33px;
  font-size: var(--fs-4);
}
@media (max-width: 1199px) {
  .btn--size-md {
    min-height: 50px;
    padding: 14px 22px;
  }
}
.btn--w-100 {
  width: 100%;
}
.btn__inner {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 1;
}
@keyframes bg-move {
  0% {
    background-position: -500px 0;
  }
  100% {
    background-position: 1000px 0;
  }
}
.btn-change {
  background: transparent;
  padding: 20px 10px;
  display: block;
  font-weight: normal;
  font-size: var(--fs-4);
  color: #747474;
  text-align: center;
  border: none;
  border-radius: 0;
  outline: none;
  cursor: pointer;
  text-decoration: underline;
  transition: all 0.4s;
}
.btn-change.disabled, .btn-change:disabled {
  opacity: 0.5;
  user-select: none;
  pointer-events: none;
}
.btn-change:hover {
  text-decoration: none;
}

.text-overflow {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.content > :first-child {
  margin-top: 0;
}
.content > :last-child {
  margin-bottom: 0;
}
.content p {
  margin: 0 0 1.2em;
}

.error-form {
  font-weight: bold;
  font-size: 10px;
  line-height: 1;
  color: rgba(255, 0, 0, 0.8);
}

.contacts-link {
  display: inline-flex;
  align-items: center;
  line-height: 1;
  transition: all 0.4s;
  transition-property: color;
}
.contacts-link:hover {
  text-decoration: none;
  color: #797DF2;
}
.contacts-link__icon {
  width: 1em;
  height: 1em;
  margin-right: 0.4em;
  flex: 0 0 1em;
}
.input-el {
  background: transparent;
  width: 100%;
  min-height: 60px;
  padding: 14px 20px;
  display: block;
  font-style: normal;
  font-weight: normal;
  font-size: var(--fs-4);
  line-height: 1.4;
  color: #000;
  border: 1px solid #6D6D78;
  border-radius: 5px;
  outline: none;
  transition: all 0.4s;
